.page-main {
  flex-grow: 1;

  &--inner {
    padding-top: 27px;
    border-top: 1px solid $color-light-grey;

    @media (min-width: $tablet-width) {
      padding-top: 64px;
      border: none;
    }

    @media (min-width: $desktop-width) {
      padding-top: 74px;
    }
  }
}

.page-main__title {
  @include container;
  margin: 0;
  margin-bottom: 26px;
  padding-bottom: 15px;
  text-overflow: ellipsis;
  overflow: hidden;

  @media (min-width: $tablet-width) {
    margin-bottom: 67px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 68px;
  }
}
