@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/lato/Lato-Regular.woff2") format("woff2"), url("../fonts/lato/Lato-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2"), url("../fonts/oswald/oswaldregular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswald/oswaldmedium.woff2") format("woff2"), url("../fonts/oswald/oswaldmedium.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

body {
  font-size: 14px;
  line-height: 18px;
  font-family: "Lato", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  background-color: #ffffff;
  color: #000000;
}
@media (min-width: 768px) {
  body {
    font-size: 16px;
    line-height: 24px;
  }
}

.headline {
  font-size: 36px;
  line-height: 36px;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
}
@media (min-width: 768px) {
  .headline {
    font-size: 60px;
    line-height: 60px;
  }
}
.headline--smaller {
  font-size: 24px;
  line-height: 26px;
}
@media (min-width: 768px) {
  .headline--smaller {
    font-size: 36px;
    line-height: 36px;
  }
}

.caption {
  font-size: 16px;
  line-height: 20px;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .caption {
    font-size: 20px;
    line-height: 24px;
  }
}
@media (min-width: 1280px) {
  .caption {
    font-size: 20px;
    line-height: 21px;
  }
}
.caption--narrow {
  font-size: 16px;
  line-height: 18px;
}
@media (min-width: 768px) {
  .caption--narrow {
    font-size: 20px;
    line-height: 30px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.button {
  font-size: 16px;
  line-height: 20px;
  font-family: "Oswald", "Arial", sans-serif;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  text-decoration: none;
  text-transform: uppercase;
  text-align: center;
  border: none;
  background-color: #68b738;
  color: #ffffff;
  padding: 7px;
  cursor: pointer;
}
@media (min-width: 768px) {
  .button {
    font-size: 20px;
    line-height: 20px;
    padding: 14px;
  }
}
.button--catalog {
  padding: 10px;
}
@media (min-width: 768px) {
  .button--catalog {
    font-size: 16px;
    line-height: 20px;
  }
}
.button:hover {
  background-color: #5eaa2f;
}
.button:active {
  background-color: #5eaa2f;
  color: rgba(255, 255, 255, 0.3);
}
.button:focus {
  outline: 2px solid #68b738;
  outline-offset: 2px;
}
.button:disabled {
  background-color: #444444;
  opacity: 0.3;
}
.button--second {
  color: #444444;
  background-color: #f2f2f2;
}
.button--second:hover {
  background-color: #ebebeb;
}
.button--second:active {
  background-color: #ebebeb;
  color: rgba(68, 68, 68, 0.3);
}

.page {
  height: 100vh;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.main-header {
  min-height: 65px;
}
@media (min-width: 768px) {
  .main-header {
    padding-left: 20px;
    padding-right: 20px;
    display: flex;
    gap: 93px;
    justify-content: space-between;
    min-height: 52px;
  }
}
@media (min-width: 768px) and (min-width: 768px) {
  .main-header {
    padding-left: 30px;
    padding-right: 30px;
  }
}
@media (min-width: 768px) and (min-width: 1440px) {
  .main-header {
    padding-left: 110px;
    padding-right: 110px;
  }
}
@media (min-width: 1280px) {
  .main-header {
    min-height: 112px;
    padding-top: 53px;
  }
}

.main-header__logo {
  min-height: 65px;
  position: relative;
}
.main-header__logo[href]:hover {
  opacity: 0.8;
}
.main-header__logo[href]:active {
  opacity: 0.6;
}
@media (max-width: 767.98px) {
  .main-header__logo[href]:focus {
    outline: none;
  }
  .main-header__logo[href]:focus::after {
    content: "";
    position: absolute;
    top: 6px;
    left: 14px;
    width: 200px;
    height: 50px;
    border: 2px solid #68b738;
  }
}
@media (min-width: 768px) {
  .main-header__logo[href]:focus {
    outline: 2px solid #68b738;
    outline-offset: 2px;
  }
}
@media (min-width: 768px) {
  .main-header__logo {
    min-height: 51px;
    flex-shrink: 0;
  }
}

.main-nav {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}
.main-nav--closed .main-nav__toggle {
  background-image: url("../img/sprite.svg#menu-burger");
}
.main-nav--opened .main-nav__toggle {
  background-image: url("../img/sprite.svg#menu-cross");
  right: 23px;
  width: 18px;
  height: 18px;
}
.main-nav--opened .main-nav__list {
  top: 65px;
  z-index: 1;
}
@media (min-width: 768px) {
  .main-nav {
    position: relative;
  }
}
@media (min-width: 1280px) {
  .main-nav {
    max-width: 50%;
  }
}

@media (max-width: 767.98px) {
  .main-nav--closed .main-nav__site-item {
    display: none;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav--nojs.main-nav {
    position: static;
    min-height: 0;
  }
  .main-nav--nojs .main-nav__site-item {
    display: block;
  }
  .main-nav--nojs {
    position: relative;
  }
}
.main-nav__toggle {
  position: absolute;
  top: 23px;
  right: 20px;
  width: 24px;
  height: 18px;
  border: none;
  background-color: transparent;
  background-repeat: no-repeat;
  background-position: center;
  padding: 0;
  cursor: pointer;
  z-index: 1;
  outline-offset: 3px;
}
.main-nav__toggle:hover {
  opacity: 0.8;
}
.main-nav__toggle:active {
  opacity: 0.6;
}
.main-nav__toggle:focus {
  outline: 2px solid #68b738;
  outline-offset: 2px;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.site {
  font-family: "Oswald", "Arial", sans-serif;
  text-transform: uppercase;
  display: flex;
  flex-wrap: wrap;
  min-width: 320px;
  width: 100%;
}
@media (max-width: 767.98px) {
  .site {
    font-size: 20px;
    line-height: 20px;
    font-weight: 500;
    flex-direction: column;
    position: relative;
    background-color: #ffffff;
  }
}
@media (min-width: 768px) {
  .site {
    font-size: 18px;
    line-height: 24px;
    max-width: 445px;
    gap: 10px 35px;
  }
}
@media (min-width: 1280px) {
  .site {
    font-size: 20px;
    line-height: 30px;
    margin-left: 130px;
    margin-bottom: 6px;
    color: #ffffff;
    max-width: 480px;
  }
}

.site__item {
  text-align: center;
  position: relative;
}
@media (max-width: 767.98px) {
  .site__item {
    padding-left: 20px;
    padding-right: 20px;
  }
}
@media (max-width: 767.98px) and (min-width: 768px) {
  .site__item {
    padding-left: 30px;
    padding-right: 30px;
  }
}
@media (max-width: 767.98px) and (min-width: 1440px) {
  .site__item {
    padding-left: 110px;
    padding-right: 110px;
  }
}
@media (max-width: 767.98px) {
  .site__item::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 1px;
    background: #e6e6e6;
  }
  .site__item:first-of-type::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 1px;
    background: #e6e6e6;
  }
}
@media (min-width: 768px) {
  .site__item--current a {
    border-bottom: 2px solid #68b738;
  }
}
@media (min-width: 1280px) {
  .site__item--current a {
    border-bottom-color: #ffffff;
  }
}
.site__item--current .site__link--inner {
  border-bottom-color: #68b738;
}

.site__link {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 22px 22px 24px;
  text-decoration: none;
  color: inherit;
}
.site__link[href]:hover {
  opacity: 0.6;
}
.site__link[href]:active {
  opacity: 0.3;
}
.site__link[href]:focus {
  outline: 2px solid #68b738;
  outline-offset: 6px;
}
@media (min-width: 768px) {
  .site__link {
    padding: 8px 0;
    margin-top: 10px;
  }
}
@media (min-width: 1280px) {
  .site__link {
    padding: 7px 0;
    margin-top: 8px;
  }
  .site__link[href]:focus {
    outline-color: #ffffff;
  }
}
@media (min-width: 768px) {
  .site__link--inner {
    padding: 8px 0;
    margin-top: 9px;
  }
}
@media (min-width: 1280px) {
  .site__link--inner {
    padding: 8px 0;
    margin-top: 8px;
    color: #111111;
  }
  .site__link--inner[href]:focus {
    outline-color: #68b738;
  }
}

@media (max-width: 767.98px) {
  .logo {
    padding-left: 20px;
    padding-right: 20px;
    display: grid;
    grid-template-columns: 1fr auto 1fr;
    align-items: center;
  }
}
@media (max-width: 767.98px) and (min-width: 768px) {
  .logo {
    padding-left: 30px;
    padding-right: 30px;
  }
}
@media (max-width: 767.98px) and (min-width: 1440px) {
  .logo {
    padding-left: 110px;
    padding-right: 110px;
  }
}
@media (min-width: 768px) {
  .logo {
    display: flex;
    align-items: center;
    padding-top: 6px;
    width: 174px;
    height: 52px;
  }
}
@media (min-width: 1280px) {
  .logo {
    width: 202px;
    height: 60px;
  }
}

@media (min-width: 768px) {
  .logo__icon {
    width: 61px;
    height: 50px;
  }
}
@media (min-width: 1280px) {
  .logo__icon {
    width: 70px;
    height: 59px;
  }
}

.logo__text {
  color: #111111;
}
@media (min-width: 768px) {
  .logo__text {
    margin-left: 12px;
    color: #444444;
  }
}
@media (min-width: 1280px) {
  .logo__text {
    width: 118px;
    height: 22px;
    margin-top: 0;
    margin-left: 14px;
    align-self: center;
  }
}

.page-main {
  flex-grow: 1;
}
.page-main--inner {
  padding-top: 27px;
  border-top: 1px solid #d9d9d9;
}
@media (min-width: 768px) {
  .page-main--inner {
    padding-top: 64px;
    border: none;
  }
}
@media (min-width: 1280px) {
  .page-main--inner {
