  }
}

.page-main__title {
  padding-left: 20px;
  padding-right: 20px;
  margin: 0;
  margin-bottom: 26px;
  padding-bottom: 15px;
  text-overflow: ellipsis;
  overflow: hidden;
}
@media (min-width: 768px) {
  .page-main__title {
    padding-left: 30px;
    padding-right: 30px;
  }
}
@media (min-width: 1440px) {
  .page-main__title {
    padding-left: 110px;
    padding-right: 110px;
  }
}
@media (min-width: 768px) {
  .page-main__title {
    margin-bottom: 67px;
  }
}
@media (min-width: 1280px) {
  .page-main__title {
    margin-bottom: 68px;
  }
}

.hero {
  padding-left: 20px;
  padding-right: 20px;
  position: relative;
  padding-top: 27px;
  margin-bottom: 20px;
}
@media (min-width: 768px) {
  .hero {
    padding-left: 30px;
    padding-right: 30px;
  }
}
@media (min-width: 1440px) {
  .hero {
    padding-left: 110px;
    padding-right: 110px;
  }
}
@media (max-width: 767.98px) {
  .hero {
    color: #ffffff;
    text-align: center;
    display: flex;
    align-items: center;
    flex-direction: column;
  }
}
@media (min-width: 768px) {
  .hero {
    min-height: 874px;
    padding-top: 64px;
    padding-left: 120px;
    margin-bottom: 0;
  }
}
@media (min-width: 1280px) {
  .hero {
    min-height: 582px;
    padding-block: 109px 185px;
    padding-left: 190px;
    margin-bottom: 80px;
    position: static;
  }
}
.hero::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 302px;
  background-color: #68b738;
  opacity: 0.85;
  z-index: -1;
}
@media (min-width: 768px) {
  .hero::before {
    display: none;
  }
}
@media (min-width: 1280px) {
  .hero::before {
    display: block;
    width: 50%;
    height: 694px;
  }
}
.hero::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 302px;
  background-color: #444444;
  background-image: url("../img/hero/cat-bg-mobile.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  z-index: -2;
}
@media (min-resolution: 2dppx) {
  .hero::after {
    background-image: url("../img/hero/cat-bg-mobile@2x.jpg");
  }
}
@media (min-width: 768px) {
  .hero::after {
    display: none;
  }
}
@media (min-width: 1280px) {
  .hero::after {
    display: block;
    background-image: url("../img/hero/cat-bg-desktop.jpg");
    background-position: left;
    background-size: cover;
    left: 50%;
    width: 50%;
    height: 694px;
  }
}
@media (min-width: 1280px) and (min-resolution: 2dppx) {
  .hero::after {
    background-image: url("../img/hero/cat-bg-desktop@2x.jpg");
  }
}

.hero__title {
  margin: 0;
  margin-bottom: 20px;
  padding-bottom: 5px;
  max-width: 250px;
  max-height: 150px;
  overflow: hidden;
  text-overflow: ellipsis;
}
@media (min-width: 768px) {
  .hero__title {
    max-width: 575px;
    max-height: min-content;
    margin-bottom: 36px;
  }
}
@media (min-width: 1280px) {
  .hero__title {
    margin-bottom: 36px;
  }
}

.hero__slogan {
  font-size: 14px;
  line-height: 14px;
  margin: 0;
  margin-bottom: 29px;
}
@media (min-width: 768px) {
  .hero__slogan {
    font-size: 20px;
    line-height: 20px;
    max-width: 575px;
    margin-bottom: 49px;
  }
}
@media (min-width: 1280px) {
  .hero__slogan {
    margin-bottom: 52px;
  }
}

@media (max-width: 767.98px) {
  .hero__link {
    font-size: 14px;
    line-height: 18px;
    padding: 11px;
    order: 1;
  }
}
@media (min-width: 768px) {
  .hero__link {
    max-width: 244px;
    padding: 17px;
  }
}

.hero__image {
  display: block;
  margin: 0 auto 3px;
}
@media (min-width: 768px) {
  .hero__image {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 709px;
    height: 609px;
    z-index: -1;
    margin-bottom: 0;
  }
}
@media (min-width: 1280px) {
  .hero__image {
    top: -368px;
    left: 368px;
    transform: none;
    width: 552px;
    height: 499px;
  }
}

@media (min-width: 1280px) {
  .hero__image-wrapper {
    position: relative;
  }
}

.programs-block {
  padding-left: 20px;
  padding-right: 20px;
  margin-bottom: 20px;
}
@media (min-width: 768px) {
  .programs-block {
    padding-left: 30px;
    padding-right: 30px;
  }
}
@media (min-width: 1440px) {
  .programs-block {
    padding-left: 110px;
    padding-right: 110px;
  }
}
@media (min-width: 768px) {
  .programs-block {
