}
.program::before {
  content: "";
  position: absolute;
  background-repeat: no-repeat;
  background-size: contain;
}
.program--slim::before {
  top: 21px;
  left: 22px;
  width: 50px;
  height: 53px;
  background-image: url("../img/sprite.svg#cat-slim");
}
.program--pro::before {
  top: 24px;
  left: 13px;
  width: 67px;
  height: 50px;
  background-image: url("../img/sprite.svg#cat-pro");
}
@media (min-width: 768px) {
  .program {
    padding: 77px 316px 53px 53px;
  }
  .program--slim::before {
    top: 80px;
    right: 61px;
    left: auto;
    width: 200px;
    height: 211px;
  }
  .program--pro::before {
    top: 91px;
    right: 29px;
    left: auto;
    width: 268px;
    height: 200px;
  }
}
@media (min-width: 1280px) {
  .program {
    padding: 77px 61px 55px 52px;
  }
