    padding-left: 162px;
    margin-bottom: 73px;
  }
}

.program__text {
  position: relative;
  margin: 0;
  margin-bottom: 38px;
}
@media (max-width: 767.98px) {
  .program__text {
    color: #444444;
  }
  .program__text::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 1px;
    bottom: -23px;
    left: 0;
    background-color: #d9d9d9;
  }
}
@media (min-width: 768px) {
  .program__text {
    margin-bottom: 48px;
  }
}
@media (min-width: 1280px) {
  .program__text {
    margin-bottom: 23px;
    min-width: 451px;
  }
}

.program__link {
  display: inline-block;
  position: relative;
  text-decoration: none;
  color: inherit;
  line-height: 16px;
  margin-top: auto;
  padding: 4px 56px 4px 0;
}
.program__link::after {
  content: "";
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 17px;
  width: 24px;
  height: 12px;
  mask-image: url("../img/sprite.svg#arrow-right");
  mask-position: right;
  background-color: #000000;
}
.program__link:hover::after, .program__link:active::after {
  right: 9px;
  width: 32px;
  mask-position: left;
}
.program__link:active {
  opacity: 0.3;
}
