.field {
  font-size: inherit;
  line-height: inherit;
  font-family: inherit;
  text-transform: uppercase;
  width: 100%;
  padding: 13px 19px 12px;
  border: 1px solid #e7e7e7;
  border-radius: 0;
  color: #444444;
  background-color: #ffffff;
}
.field--iconed {
  padding-inline: 14px 34px;
}
.field--textarea {
  height: 150px;
  vertical-align: top;
  padding: 9px 12px 9px 15px;
  resize: none;
}
@media (min-width: 768px) {
  .field--textarea {
    height: 174px;
  }
}
.field--textarea::placeholder {
  color: #444444;
}
@media (min-width: 768px) {
  .field {
    padding: 10px 19px;
  }
}
.field::-webkit-outer-spin-button, .field::-webkit-inner-spin-button {
  appearance: none;
  margin: 0;
}
.field[type=number] {
  appearance: textfield;
}
.field:hover {
  border-color: #cdcdcd;
}
.field:active {
  opacity: 0.6;
}
.field:focus-visible {
  outline: none;
  border-color: #444444;
  box-shadow: inset 0 0 0 1px #444444;
}
.field:focus-visible + .form__field-icon {
  background-color: #68b738;
}
.field:disabled {
  opacity: 0.3;
}
.field[required]:not(:focus):not(:placeholder-shown):invalid {
  border-color: #ff8282;
  box-shadow: inset 0 0 0 1px #ff8282;
}
.field[required]:not(:focus):not(:placeholder-shown):invalid + .form__field-icon {
  background-color: #ff8282;
}
.field--error {
  border-color: #ff8282;
  box-shadow: inset 0 0 0 1px #ff8282;
}
.field--error + .form__field-icon {
  background-color: #ff8282;
}

.control {
  gap: 15px;
  cursor: pointer;
  max-width: fit-content;
}
@media (min-width: 768px) {
  .control {
    gap: 20px;
  }
}
.control:hover .control__mark {
  border-color: #bababa;
}
.control:active {
  opacity: 0.8;
}
.control:focus-within {
  outline-offset: 2px;
  outline: 2px solid #68b738;
}

.control__mark {
  flex-shrink: 0;
  align-self: start;
  position: relative;
  width: 30px;
  height: 30px;
  border: 1px solid #cdcdcd;
}
@media (min-width: 768px) {
  .control__mark {
    width: 40px;
    height: 40px;
  }
}

.control__input[type=checkbox]:checked + .control__mark::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 19px;
  height: 15px;
  mask-image: url("../img/sprite.svg#icon-check");
  mask-repeat: no-repeat;
  mask-position: center;
  mask-size: contain;
  background-color: #68b738;
}
@media (min-width: 768px) {
  .control__input[type=checkbox]:checked + .control__mark::before {
    width: 26px;
    height: 26px;
  }
}

.control__input[type=radio] + .control__mark {
  border-radius: 50%;
}

.control__input[type=radio]:checked + .control__mark::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 13px;
  height: 13px;
  background-color: #68b738;
  border-radius: 50%;
}
@media (min-width: 768px) {
  .control__input[type=radio]:checked + .control__mark::before {
    width: 18px;
    height: 18px;
  }
}

.control__input:disabled + .control__mark,
.control__input:disabled ~ .control__label {
  opacity: 0.3;
}

.space::after {
  content: "\a";
}
@media (max-width: 767.98px) {
  .space--mobile::after {
    white-space: pre;
  }
}
@media (min-width: 768px) and (max-width: 1279.98px) {
  .space--tablet::after {
    white-space: pre;
  }
}
@media (min-width: 1280px) {
  .space--desktop::after {
    white-space: pre;
  }
}